import * as types from '../../mutation_type';
export default {
    setPaperID({commit}, paperID) {
        commit(types.CHECK_PAPER_RESULT, paperID);
    },
    setTemplateType({commit}, temType) {
        commit(types.GET_TEMPLATE_TYPE, temType);
    }
}



