                _this.requestRefrecePerson();
                _this.requestPatientStatus();
                _this.requestOriginalOrgs();
                let patientId = _this.$store.state.mystudent.currentPatientInfo.ID;
                api.getPatientAgentDetails(patientId).then(res => {
                    // pg54
                    let o = res.data;
                    _this.saveCurrentPatientInfo(o);
                    _this.userInfo = res.data;
                    let birthData = res.data.BirthDate
                    _this.birthDayStr = birthData
                    _this.userInfo.BirthDate = new Date(birthData);
                    _this.userNamePrepend = res.data.UserName ? true : false;
                    _this.imageUrl = res.data.Logo;
                    if (_this.userInfo.Member1 == null) {
                        _this.userInfo.Member1 = {
                            ID: "",
                            Name: "",
                            Age: '',
                            Degree: '',
                            WorkUnit: '',
                            Title: '',
