import * as types from '../../mutation_type';

export default {
    [types.CHECK_PAPER_RESULT](state, paperID) {
        state.checkPaperID = paperID;
    },
    [types.GET_TEMPLATE_TYPE](state, temType) {
        state.templateType = temType;
    }
}



